body {
  background: url("./images/S9l3Wg.jpg");
  background-size: cover;
  font-family: "Times New Roman", "Times", "serif";
  font-weight: 1000;
}

root {
  font-size: 10px;
}

#drum-machine {
  display: grid;
  padding: 0;
  width: 40rem;
  height: 20rem;
  justify-content: center;
}

#display {
  margin-left: 50%;
  border: 20px solid silver;
  text-align: center;
  background: radial-gradient(
    circle,
    rgba(2, 0, 36, 1) 7%,
    rgba(33, 33, 105, 1) 27%,
    rgba(38, 38, 114, 1) 38%,
    rgba(41, 41, 121, 1) 46%,
    rgba(34, 34, 107, 1) 57%,
    rgba(36, 36, 110, 1) 100%,
    rgba(0, 212, 255, 1) 100%
  );
  /**background: radial-gradient(
      circle,
      rgb(196, 194, 194) 10%,
       rgb(0, 0, 139) 25%, 
       rgb(196, 194, 194) 30%,
        rgb(0, 0, 139) 45%, 
        rgb(196, 194, 194) 60%,
         rgb(0, 0, 139) 85%,     
    );*/
  width: 100%;
  height: 100%;
  margin-top: 5%;
}

#soundButton {
  width: 20rem;
  height: 20rem;
  max-width: 8rem;
  margin: 1px;
}

#audio {
  background-color: gray;
  border: 1px solid silver;
  margin: 5px;
  padding: 15px;
}

#soundinfo {
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  height: 5rem;
  align-items: center;
}

#notes {
  text-align: center;
  border: 3px solid darkgray;
  background-color: gray;
  color: #005493;
  height: 2rem;
  width: 10rem;
  margin-right: 1rem;
}

#ChangingSounds button {
  background-color: lightgray;
  color: lightblue;
  margin: 50px;
  font-weight: 900;
}
